<template>
  <v-btn-toggle
  
    @change="emojichange"
    v-model="$store.state.emoji"
    class="row mt-5 ml-lg-4 ml-1 ml-md-5"
    style="background: transparent !important"
  >
    <template v-for="(img, j) in $store.state.arrayEmojis">
      <v-btn
        :value="img.image"
        :key="j + 'noPrincipal'"
        fab
        :color="colorButton"
        style="font-size: 30px; opacity: 1"
      >
        <v-img
          width="30px"
          height="30px"
          contain
          style="flex: 0 1 auto"
          :src="img.image"
        ></v-img>
      </v-btn>
    </template>
  </v-btn-toggle>
</template>
<script>
export default {
  name: "ButtonsEmoji",
  props: {
    colorButton: {
      type: String,
      default: "primary",
    },
  },
  async mounted() {
    await this.getAllEmojis();
  },
  methods: {
    emojichange() {
      this.$socket.emit("emojiChangeClient", this.$store.state.emoji);
    },
    async getAllEmojis() {
      var api = this.$store.state.URL + "/emojis/getAll";
      
      var response = await this.axios.get(api);
      
      if (response) {
        if (response.data) {
          this.$store.state.arrayEmojis = response.data.emojis;
          this.$store.state.arrayEmojis = this.$store.state.arrayEmojis.sort(function (a, b) {
            return a.type.localeCompare(b.type);
          });
        }
      } else {
        this.$store.state.arrayEmojis = [];
      }
    },
  },
  // sockets: {
  //   emojiChangeServer: async function (data) {
  //     this.$store.state.arrayEmojis = data;
  //   },
  // },
};
</script>
