<script>
// @ is an alias to /src
import NavBar from "@/components/NavBar.vue";
import Fire from "@/components/Fire.vue";
import ButtonsEmoji from "@/components/ButtonsEmoji.vue";
import ButtonBackground from "@/components/ButtonBackground.vue";
import MessageRandom from "@/components/MessageRandom.vue";

export default {
  name: "Home",
  components: {
    NavBar,
    Fire,
    ButtonsEmoji,
    MessageRandom,
    ButtonBackground
  },
  data: () => ({
    toggleEmoji: false,
    maxSuscribers: 20,
    maxFollows: 20,
    copyDiscord: false,
    copiedDiscord: false,
    copyGmail: false,
    copiedGmail: false,
    channel: {},
    messageSub: "",
    texturePack: [],
    myDiscord: {},
    donatorSelected: {},
    messageDonatorsShow: false,
    servers: [
      {
        name: "StarWorld",
        img: "https://yt3.ggpht.com/ytc/AKedOLRSf4MBA9_GYealKQU6ZSdYkV4spkfQ6P2OvJSG=s176-c-k-c0x00ffffff-no-rj",
        pc: "starworldmc.ml",
      },
      {
        name: "Mycraft",
        img: "https://yt3.ggpht.com/ytc/AAUvwniGOZkdhAxndcIhIQbZ_dvXV4WUEN4ndsddWgp3=s88-c-k-c0x00ffffff-no-rj",
        pc: "server.mycraft.es",
      },
      {
        name: "Mooncraft",
        img: "https://yt3.ggpht.com/ytc/AAUvwnjCrsvO1L1kR5CVjJejZbIdCWORfKcdvRYivTfU8g=s88-c-k-c0x00ffffff-no-rj",
        pc: "mooncraft.es",
      },
      {
        name: "HyCraft",
        img: "https://yt3.ggpht.com/ytc/AAUvwniItYTVKevfu3Sp8SWF8dIC5uAOTgeB8HFUPweJfQ=s88-c-k-c0x00ffffff-no-rj",
        pc: "mc.hycraft.us",
      },
      {
        name: "ZoneCraft",
        img: "https://yt3.ggpht.com/ytc/AKedOLSidJcsYRuy1FkCj-P2_opVWnt4EhjDoQkPpTYJ=s176-c-k-c0x00ffffff-no-rj",
        pc: "play.zonecraft.es",
      },
      {
        name: "Splindux",
        img: "https://cdn.discordapp.com/attachments/688566101910880275/830494735956836382/636564639831776656.png",
        pc: "mc.splindux.com",
      },
    ],
    moderators: [],
    showListMod: false,
    showListFollowes: false,
    showListSuscribers: false,
    showListDonators: false,
    mySubscribersYT: [],
    myFollowersTwitch: [],
    myDonators: [],
  }),
  sockets: {
    async connect() {
      console.log("Esta conectado");
    },

    async disconnect() {
      console.log("Esta desconectado");
    },
  },
  methods: {
    formatDate(time) {
      var date = new Date(time * 1000).toLocaleString();
      return date;
    },
    totalDonation(items) {
      var accountant = 0;
      for (var i = 0; i < items.length; i++) {
        accountant += parseInt(items[i].amount);
      }
      return accountant;
    },
    selectDonator(items) {
      this.donatorSelected.name = items[0].name;
      this.messageDonatorsShow = true;
      this.donatorSelected = items.sort(function (a, b) {
        return b.created_at - a.created_at;
      });
    },
    getDiscord() {
      var api = this.$store.state.URL + "/channels/myDiscordDetails";

      this.axios
        .get(api)
        .then((response) => {
          if (response.data == null) {
            console.log("Error");
          }
          return response.data;
        })
        .then((data) => {
          this.myDiscord = data.myDiscord;
        })
        .catch((e) => {
          console.log(e);
        });
    },
    timeOut(variable) {
      this[variable] = true;
      setTimeout(() => {
        this[variable] = false;
      }, 3000);
    },
    copyText(text) {
      var aux = document.createElement("input");
      aux.setAttribute("value", text);
      document.body.appendChild(aux);
      aux.select();
      document.execCommand("copy");
      document.body.removeChild(aux);
    },
    changeMessageRandom() {
      this.$socket.emit("messageRandom", this.$store.state.messageSubs);
    },
    getMyChannel() {
      var api = this.$store.state.URL + "/channels/myChannels";
      this.axios
        .get(api)
        .then((response) => {
          if (response.data == null) {
            console.log("Error");
          }
          return response.data;
        })
        .then((data) => {
          this.channel = data.myChannel;
        })
        .catch((e) => {
          console.log(e);
        });
    },
    getTexturePack() {
      var api = this.$store.state.URL + "/channels/texturePack";
      this.axios
        .get(api)
        .then((response) => {
          if (response.data == null) {
            console.log("Error");
          }
          return response.data;
        })
        .then((data) => {
          this.texturePack = data.texturePack;
          this.texturePack = this.texturePack.sort(function (a, b) {
            return a.channelName.localeCompare(b.channelName);
          });
        })
        .catch((e) => {
          console.log(e);
        });
    },
    getMods() {
      this.showListMod = !this.showListMod;
      if (this.showListMod) {
        this.moderators = [];
        var api = this.$store.state.URL + "/channels/modYoutube";
        this.axios
          .get(api)
          .then((response) => {
            if (response.data == null) {
              console.log("Error");
            }
            return response.data;
          })
          .then((data) => {
            this.moderators = data.moderators;
            this.moderators = this.moderators.sort(function (a, b) {
              return a.channelName.localeCompare(b.channelName);
            });
          })
          .catch((e) => {
            console.log(e);
          });
      }
    },

    getDonators() {
      this.showListDonators = !this.showListDonators;
      if (this.showListDonators) {
        this.myDonators = [];
        var api = this.$store.state.URL + "/channels/myDonations";
        this.axios
          .get(api)
          .then((response) => {
            if (response.data == null) {
              console.log("Error");
            }
            return response.data.myDonators;
          })
          .then((myDonators) => {
            this.myDonators = myDonators;
            this.myDonators = this.myDonators.sort((a, b) => {
              return this.totalDonation(b) - this.totalDonation(a);
            });
          })
          .catch((e) => {
            console.log(e);
          });
      }
    },

    getSubscribers(max = 7) {
      this.showListSuscribers = !this.showListSuscribers;
      if (this.showListSuscribers) {
        this.mySubscribersYT = [];
        var api = this.$store.state.URL + "/channels/mySubscribersYT/" + max;
        this.axios
          .get(api)
          .then((response) => {
            if (response.data == null) {
              console.log("Error");
            }
            return response.data.mySubscribers;
          })
          .then((mySubscribers) => {
            this.mySubscribersYT = mySubscribers;
          })
          .catch((e) => {
            console.log(e);
          });
      }
    },

    getFollowers(max = 7) {
      this.showListFollowes = !this.showListFollowes;
      if (this.showListFollowes) {
        this.myFollowersTwitch = [];
        var api = this.$store.state.URL + "/channels/myFollowTwitch/" + max;
        this.axios
          .get(api)
          .then((response) => {
            if (response.data == null) {
              console.log("Error");
            }
            return response.data.myFollowers;
          })
          .then((myFollowers) => {
            this.myFollowersTwitch = myFollowers;
          })
          .catch((e) => {
            console.log(e);
          });
      }
    },
  },
  mounted() {
    this.getDiscord();
    this.getMyChannel();
    this.$socket.connect();
    // this.getTexturePack();
    // this.axios
    //   .get("https://discord.com/api/v8/guilds/747586757888835655?with_counts=true", {
    //     headers: {

    //     },
    //   })
    //   .then((response) => {
    //     console.log(response);
    //     return response.data;
    //   })
    //   .then((data) => {})
    //   .catch((e) => {
    //     console.log(e);
    //   });
  },
};
</script>


