.toogleEmoji .toggle.enable{
  border-color: #4caf50 !important;
}

.toogleEmoji .v-input--switch__track{
  color: white !important
}

@media (max-width: 600px) {
  .toogleEmoji .toggle{
    width: auto !important;
    margin: 0px auto;  
  }
}

@keyframes latidos {
    from { padding:  none; }
    50% { transform: scale(0.9); }
    to { transform: none; }
}
